.link_button {
	display: inline-flex;
	align-items: center;
	text-decoration: none;
	font-size: 2rem;
	color: inherit;
	padding: 1rem;
	border-radius: 4px;
	transition: background-color 0.3s;
	background-color: rgba(0, 0, 0, 0.6);
	margin: 0.5rem;

	&:hover {
		background-color: rgba(0, 0, 0, 0.8);
		& svg {
			filter: drop-shadow(0 0 0.1rem rgba(255, 255, 255, 0.6));
		}

	}

	.sr_only {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		margin: -1px;
		overflow: hidden;
		clip: rect(0, 0, 0, 0);
		border: 0;
	}
}