*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

    /* Outros estilos */

  /* Estilizando a barra de rolagem */
  ::-webkit-scrollbar {
    width: 12px; /* Largura da barra de rolagem */
  }

  ::-webkit-scrollbar-track {
    background: #000; /* Cor do trilho da barra de rolagem */
    border-radius: 10px; /* Borda arredondada do trilho */
  }

  ::-webkit-scrollbar-thumb {
    background: #005969; /* Cor do polegar da barra de rolagem */
    border-radius: 10px; /* Borda arredondada do polegar */
  }

  ::-webkit-scrollbar-thumb:hover {
    background: #555; /* Cor do polegar ao passar o mouse */
  }

  /* Para navegadores que suportam a pseudo-classe scrollbar */
  scrollbar-width: thin; /* Largura da barra de rolagem */
  scrollbar-color: #008197 #000; /* Cor do polegar e do trilho */
}