.main_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #FFF;
    background-color: #000016;
}

// Home
.home_container {
    min-height: 100vh;
    background-image: url('../../sources/bg3.webp');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.home_card {
    width: 90%;
    max-width: 700px;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 2rem;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (max-width: 820px) {
        padding: 1rem;
    }
}

.home_card-title {
    font-size: 3rem;
    margin-bottom: 1rem;
    text-align: center;
    text-shadow: 0 0 0.4rem rgba(255, 255, 255, 0.2);
    @media (max-width: 820px) {
        padding-top: 4rem;
        font-size: 2.4rem;
    }
}

.home_card-stacks {
    font-size: 1.6rem;
    text-align: center;
    margin-bottom: .5rem;
    @media (max-width: 820px) {
        padding: .6rem;
        font-size: 1.4rem;
    }
}

.home_card-abbout {
    font-size: 1.2rem;
    text-align: justify;
    margin: .4rem 0;
    @media (max-width: 820px) {
        padding: .8rem;
        text-align: initial;
        font-size: 1rem;
        margin: 0;
    }
}

.home_links {
    padding: 1rem;
    display: flex;
    justify-content: space-between;

    @media (max-width: 820px) {
        padding: 0;
    }
}

// Skills
.skills {
    width: 100%;
    margin-top: 1rem;
    min-height: 40vh;
    display: flex;
    flex-direction: column;
    border-bottom: 2px solid #FFF;
}

.skills_title {
    font-size: 2.4rem;
    font-weight: bold;
    text-align: center;
    margin: 0 auto;
    text-shadow: 0 0 0.4rem rgba(255, 255, 255, 0.4);
}

.skills_container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 1rem;
}

.skills_item {
    margin: .5rem;
    padding: 1rem 2rem;
    font-size: 1.6rem;
    background-color: #333;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: background-color 0.3s;
    text-shadow: 0 0 0.1rem rgba(255, 255, 255, 0.6);

    &:hover {
        background-color: rgba(0, 0, 0, 0.8);
    }

    @media (max-width: 820px) {
        padding: .5rem 1rem;
        font-size: 1.2rem;
    }
}

.skills_more {
    margin-top: 1rem;
    text-align: center;
    text-shadow: 0 0 0.1rem rgba(255, 255, 255, 0.6);
    @media (max-width: 820px) {
        font-size: 1.4rem;
    }
}

.skills_title-more {
    font-size: 1.6rem;
    text-align: center;
    margin: 0 auto;
    text-shadow: 0 0 0.4rem rgba(255, 255, 255, 0.4);
    display: flex;
}

.skills_item-more {
    margin: .5rem;
    padding: 1rem 2rem;
    font-size: 1.6rem;
    background-color: #333;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    transition: background-color 0.3s;
    text-shadow: 0 0 0.1rem rgba(255, 255, 255, 0.6);

    &:hover {
        background-color: rgba(0, 0, 0, 0.8);
    }

    @media (max-width: 820px) {
        padding: .5rem 1rem;
        font-size: 1.2rem;
    }
}

.skills_more-temp {
    margin: 1rem 0;
    font-size: 1.4rem;
    text-align: center;
    text-shadow: 0 0 0.1rem rgba(255, 255, 255, 0.6);

    @media (max-width: 820px) {
        font-size: 1.2rem;
    }
}

// Projetos
.projects {
    width: 90%;
    max-width: 1200px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.projects_title {
    font-size: 2.4rem;
    font-weight: bold;
    text-align: center;
    margin: 0 auto;
    padding: 1rem 0;
    text-shadow: 0 0 0.4rem rgba(255, 255, 255, 0.4);
}

.projects_card {
    background-color: rgba(0, 0, 0, 1);
    border-radius: 0.5rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 1rem;
    border: 1px solid #FFF;
    cursor: pointer;



    @media (max-width: 820px) {
        flex-direction: column;
    }
}

.projects_card-image {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 820px) {
        width: 100%;
        flex-direction: column;
    }
}

.projects_card-about {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;


    @media (max-width: 820px) {
        width: 100%;
    }
}

.card_image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0.5rem;
}

.card_title {
    font-size: 2rem;
    text-align: center;
    text-shadow: 0 0 0.4rem rgba(255, 255, 255, 0.4);
}

.card_desc {
    font-size: 1.2rem;
    text-align: center;
    margin: .4rem;
    
    @media (max-width: 820px) {
        padding: .8rem;
        text-align: initial;
        font-size: 1rem;
        margin: 0;
    }
}

.card_stack {
    width: 100%;
    font-size: 1.4rem;
    text-align: center;
    margin: .5rem;
    padding-top: 2rem;

    @media (max-width: 820px) {
        padding: .6rem;
        font-size: 1.2rem;
    }
}

.card_reverse {
    flex-direction: row-reverse;

    @media (max-width: 820px) {
        flex-direction: row;
        flex-wrap: wrap;
    }
}

.projects_more {
    text-align: center;
    text-decoration: none;
    font-size: 2rem;
    color: #fff;

    @media (max-width: 820px) {
        font-size: 1.6rem;
    }
}

.projects_more-temp {
    margin: 1rem 0;
    font-size: 1.4rem;
    text-align: center;
    text-shadow: 0 0 0.1rem rgba(255, 255, 255, 0.6);

    @media (max-width: 820px) {
        font-size: 1.2rem;
    }
}