.logo {
	margin-top: .8rem;
	margin-left: 1rem;
	height: 1.8rem;
	font-size: 1.8rem;
	background-image: linear-gradient(109.6deg, rgba(61, 245, 167, 1) 11.2%, rgba(9, 111, 224, 1) 91.1%);
	background-size: 200% auto;
	color: #fff;
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	animation: textclip 2s linear infinite;
	display: inline-block;
}

@keyframes textclip {
	to {
		background-position: 200% center;
	}
}

.nav_container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 4rem;
	z-index: 1000;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.nav_buttons_container {
	height: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;

	&.open {
		display: flex;
	}
}

.nav_buttons {
	margin: 1rem;
	height: auto;
	font-size: 1.2rem;
	color: #FFF;
	position: relative;
	cursor: pointer;
	text-align: center;

	&:last-child {
		margin-right: 2rem;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: -2px;
		left: 0;
		width: 100%;
		height: 2px;
		background-color: #fff;
		transform: scaleX(0);
		transform-origin: left;
		transition: transform 0.3s ease;
	}

	&:hover:before {
		transform: scaleX(1);
	}

	&:hover {
		color: #aaa;
	}

	&:not(:hover):before {
		transform-origin: right;
	}
}

.burger-menu {
	display: none;
	cursor: pointer;
	width: 30px;
	height: 30px;
	position: absolute;
	right: 1rem;
	top: 50%;
	transform: translateY(-50%);

	span {
		display: block;
		width: 100%;
		height: 4px;
		background-color: #fff;
		position: absolute;
		left: 0;
		transition: transform 0.3s ease, opacity 0.3s ease;

		&:nth-child(1) {
			top: 0;
		}

		&:nth-child(2) {
			top: 45%;
			transform: translateY(-50%);
		}

		&:nth-child(3) {
			top: 80%;
		}
	}

	&.open span:nth-child(1) {
		transform: rotate(45deg) translate(5px, 5px);
	}

	&.open span:nth-child(2) {
		opacity: 0;
	}

	&.open span:nth-child(3) {
		transform: rotate(-45deg) translate(5px, -5px);
	}
}

@media (max-width: 820px) {
	.nav_buttons_container {
		flex-direction: column;
		position: fixed;
		top: 0;
		right: -100%;
		width: 70%;
		height: 100vh;
		background-color: #000;
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: left;
		padding-top: 4rem;
		transition: right 0.3s ease;

		&.open {
			right: 0;
		}

		.nav_buttons {
			margin: 10px 0;
		}
	}

	.burger-menu {
		display: block;
		right: 1.4rem;
	}
}